export class Order {
    constructor() {
        this._payment = '';
        this._address = '';
        this._email = '';
        this._phone = '';
        this._total = 0;
    }
    get payment() {
        return this._payment;
    }
    set payment(payment) {
        this._payment = payment;
    }
    get address() {
        return this._address;
    }
    set address(address) {
        this._address = address;
    }
    get email() {
        return this._email;
    }
    set email(email) {
        this._email = email;
    }
    get phone() {
        return this._phone;
    }
    set phone(phone) {
        this._phone = phone;
    }
    get total() {
        return this._total;
    }
    set total(total) {
        this._total = total;
    }
    get items() {
        return this._items;
    }
    set items(items) {
        this._items = [];
        for (const item of items) {
            this._items.push(item);
        }
    }
    submitOrder() {
        this.resetOrder();
    }
    resetOrder() {
        this._payment = '';
        this._address = '';
        this._email = '';
        this._phone = '';
        this._total = 0;
    }
}
