export class Gallery {
    constructor() {
        this._products = [];
    }
    get products() {
        return this._products;
    }
    set products(products) {
        this._products = [];
        for (const product of products) {
            this._products.push(product);
        }
    }
    get total() {
        return this._total;
    }
    set total(total) {
        this._total = total;
    }
}
