import { settings } from '../../utils/constants';
import { FormView } from './form';
export class OrderView extends FormView {
    set address(address) {
        this._address = address;
    }
    constructor(container, events) {
        super(container, events);
        this.container = container;
        this.events = events;
        this.address = container.querySelector(settings.orderSettings.address);
        this._buttonCardName = container.querySelector(settings.orderSettings.buttonCardName);
        this._buttonCashName = container.querySelector(settings.orderSettings.buttonCashName);
        this._buttonSubmit = container.querySelector(settings.orderSettings.buttonSubmit);
    }
}
