export class Basket {
    constructor() {
        this._products = [];
    }
    get products() {
        return this._products;
    }
    set products(products) {
        this._products = [];
        for (const product of products) {
            this._products.push(product);
        }
    }
    addProduct(product) {
        this._products.push(product);
    }
    removeProduct(id) {
        this._products = this._products.filter(function (product) {
            return product.id != id;
        });
    }
    clearBasket() {
        this._products = [];
    }
}
