import { CDN_URL } from '../../utils/constants';
import { Api } from '../base/api';
export class ShopApi extends Api {
    constructor(baseUrl, options = {}) {
        super(baseUrl, options);
    }
    getProductList() {
        return this.get('/product/').then((productsList) => productsList.items.map((item) => (Object.assign(Object.assign({}, item), { image: CDN_URL + item.image }))));
    }
    getProductItem(id) {
        return this.get('/product/' + id).then();
    }
    getOrder() {
        return this.get('/order/').then();
    }
}
