import { settings } from '../../utils/constants';
import { View } from './view';
export class ProductFullView extends View {
    set image(image) {
        this._image = image;
    }
    set category(category) {
        this._category = category;
    }
    set title(title) {
        this._title = title;
    }
    set description(description) {
        this._description = description;
    }
    set price(price) {
        this._price = price;
    }
    constructor(container, events) {
        super(container, events);
        this.container = container;
        this.events = events;
        this.image = container.querySelector(settings.productFullSettings.image);
        this.category = container.querySelector(settings.productFullSettings.category);
        this.description = container.querySelector(settings.productFullSettings.description);
        this.price = container.querySelector(settings.productFullSettings.price);
        this._button = container.querySelector(settings.productFullSettings.button);
    }
}
